.gpt3__header {
  display: flex;
  flex-direction: row;
}

.gpt3__header-content {
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: flex-start;
  flex-direction: column;

  margin-right: 5rem;
}

.gpt3__header-content h1 {
  font-family: var(--font-family);
  font-weight: 800;
  font-size: 62px;
  line-height: 75px; /* 120.968% */
  letter-spacing: -0.04rem;
}

.gpt3__header-content p {
  color: var(--color-text);
  font-family: var(--font-family);
  font-style: normal;
  font-weight: normal;
  font-size: 20px;
  line-height: 27px;
}

.gpt3__header-content__input {
  width: 100%;
  margin: 2rem 0 1rem;
  display: flex;
  flex-direction: row;
}

.gpt3__header-content__input input {
  flex: 2;
  width: 100%;
  min-height: 60px;
  font-family: var(--font-family);
  border-radius: 5px 0px 0px 5px;
  background: var(--color-footer);
  border: 2px solid var(--color-footer);
  color: #fff;
  padding: 0 1rem;
  outline: none;
  font-size: 20px;
  line-height: 27px;
}

.gpt3__header-content__input button {
  flex: 0.6;
  width: 100%;
  min-height: 50px;
  font-family: var(--font-family);
  font-weight: bold;
  line-height: 27px;
  border-radius: 5px 5 px 5px 5px;
  background: #ff4820;
  color: #fff;
  cursor: pointer;
  outline: none;
  padding: 0 1rem;
  border: none;
}

.gpt3__header-content__people {
  width: 100%;
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 2rem;
}

.gpt3__header-content__people img {
  width: 181.79px;
  height: 38px;
}

.gpt3__header-content__people p {
  margin-left: 1rem;
  color: #fff;
  font-family: var(--font-family);
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 38px; /* 313.423% */
  text-align: center;
}


.gpt3__header-image{
  flex: 1;
  display: flex;
  justify-content: center;
  align-items: center;
}

.gpt3__header-image img{
  width: 100%;
  height: 100%;
}

@media screen and (max-width:1050px) {
  .gpt3__header{
    flex-direction: column;
  }

  .gpt3__header-content{
    margin: 0 0 3rem;
  }
}


@media screen and (max-width:650px) {
  .gpt3__header h1{
    font-size: 48px;
    line-height: 60px;
  }

  .gpt3__header p{
    font-size: 16px;
    line-height: 24px;
  }

  .gpt3__header-content__people{
    flex-direction: column;
    margin: 0;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button{
    font-size: 16px;
    line-height: 24px;
  }
}


@media screen and (max-width:490px) {
  .gpt3__header h1{
    font-size: 36px;
    line-height: 48px;
  }

  .gpt3__header p{
    font-size: 14px;
    line-height: 24px;
  }

  .gpt3__header-content__input input,
  .gpt3__header-content__input button{
    font-size: 12px;
    line-height: 16px;
  }
}