.gpt3__cta {
  display: flex;
  justify-content: space-between;
  align-items: center;
  flex-direction: row;
  padding: 2rem;
  margin: 4rem;

  border-radius: 2rem;
  background: var(--gradient-bar);
}

.gpt3__cta-content p {
  color: #0e0e0e;
  font-family: var(--font-family);
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 30px; /* 250% */
}

.gpt3__cta-content h3 {
  font-family: var(--font-family);
  font-size: 24px;
  font-style: normal;
  font-weight: 800;
  line-height: 45px; /* 187.5% */
}

.gpt3__cta-btn {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-left: 2rem;
}

.gpt3__cta-btn button {
  border-radius: 40px;
  background: #000;

  color: #fff;
  font-family: var(--font-family);
  font-size: 18px;
  font-style: normal;
  font-weight: 700;
  line-height: 30px; /* 166.667% */
  padding: 0.5rem 1rem;

  border: none;
  outline: none;
  cursor: pointer;
  width: 150px;
}


@media screen and (max-width:650px) {
  .gpt3__cta{
    flex-direction: column;
  }

  .gpt3__cta-btn{
    margin: 2rem 0 0;
  }
}

@media screen and (max-width:650px) {
  .gpt3__cta{
    margin: 4rem 2rem;
  }

  .gpt3__cta-content h3{
    font-size: 18px;
    line-height: 32px;
  }

  .gpt3__cta-btn button{
    font-size: 14px;
    line-height: 29px;
  }
}
